'use client';
import Image from 'next/image';

const features = [
  {
    title: 'Registered & Certified Professionals',
    description:
      'We are registered tax practitioners with the Australian Tax Practitioners Board, ensuring compliance and accuracy.',
    icon: '/icons/professionals.svg',
  },
  {
    title: 'Trusted Industry Experience',
    description:
      'With years of experience in tax, accounting, and superannuation compliance, we provide expert financial guidance.',
    icon: '/icons/experience.svg',
  },
  {
    title: 'Professional & Ethical Standards',
    description:
      'As IPA and NTAA members, we uphold the highest ethical standards.',
    icon: '/icons/standards.svg',
  },
  {
    title: 'Hassle-Free Tax Lodgment',
    description:
      'We make tax return filing quick, easy, and stress-free for both individuals and businesses.',
    icon: '/icons/lodgment.svg',
  },
  {
    title: 'Personalized Support & Advice',
    description:
      'Our team offers tailored financial solutions, ensuring each client receives individualized attention.',
    icon: '/icons/support.svg',
  },
  {
    title: 'Maximum Tax Refund Guarantee',
    description:
      'Our expertise in Australian tax law ensures you receive the highest possible refund legally available.',
    icon: '/icons/refund.svg',
  },
  {
    title: 'Strict Data Security Measures',
    description:
      'We prioritize online security and implement advanced data protection to safeguard your sensitive information.',
    icon: '/icons/security.svg',
  },
  {
    title: 'Melbourne-Based, Client-Focused',
    description:
      'We take pride in serving our local community, building long-term trust-based relationships with our clients.',
    icon: '/icons/melbourne.svg',
  },
];

export default function WhyChooseUs() {
  return (
    <section className="py-16 px-4 text-center">
      <h2 className="text-3xl md:text-4xl font-bold mb-4">
        Lodge your Tax Return with Convenience and we will Assure Compliance
      </h2>
      <button className="bg-[#FE5722] text-white px-6 py-2 rounded-md font-semibold text-lg mb-10">
        WHY CHOOSE US?
      </button>
      
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8 max-w-5xl mx-auto">
        {features.map((feature, index) => (
          <div
            key={index}
            className="relative bg-white shadow-md p-6 rounded-md text-left flex flex-col items-center"
          >
            <Image src={feature.icon} alt={feature.title} width={50} height={50} />
            <h3 className="text-xl font-bold mt-4">{feature.title}</h3>
            <p className="text-gray-600 text-sm mt-2 text-center">{feature.description}</p>
          </div>
        ))}
      </div>
    </section>
  );
}
